<template>
    <div class="yearCard">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="cardTitle">私教</span>
            <span class="more" @click="toggleMore">
                <img class="more" src="../../assets/img/more.png">
                <ul v-show="isShow" class="moreMenu" id="moreMenu">
                  <li v-for="(action, index) in actions" :key="index">{{ action.link }}</li>
                </ul>
            </span>
        </div>
        <div class="container">
            <ul class="cardsInfor">
                <li>1:超值消费，购买即可享用全年不限次数课程</li>
                <li>2:可参加教练任意课程，享受一对一教学</li>
                <li>3:通过平台购买可享受超值优惠:
                    <br>
                    <span class="textOrange">平台:2000元/次</span>
                    &emsp;KO&emsp;<span class="textOrange">线下:4000元/次</span>
                    <br>
                    另外，通过平台报名购买课程免费赠送价值500元大礼包，优先报名优惠更多。
                </li>
            </ul>
        </div>
        <div class="footer">
          <div class="price">
            <span>抢购价:  ￥</span>
            <span>2000元/次</span>
          </div>
          <button class="buyBtn">立即抢购</button>
        </div>
        <div class="mask" v-show="hasMask" @click="toggleMore"></div>
    </div>
</template>

<script>
export default {
  name: 'yearCard',
  data () {
    return {
      isShow: false,
      hasMask: false,
      actions: [{
        link: '收藏'
      },
      {
        link: '分享'
      },
      {
        link: '举报'
      }]
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    toggleMore () {
      this.isShow = !this.isShow
      this.hasMask = !this.hasMask
    }
  }
}
</script>

<style scoped>
  * {
    font-size: 0.5rem;
    margin: 0;
    padding: 0;
  }

  .yearCard {
    width: 100%;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .more {
    height: 0.6rem;
    width: 0.13rem;
    position: relative;
  }

  .more img,
  .back img {
    width: 100%;
  }

  .container {
    width: calc(100% - 2rem);
    margin: 0 auto;
  }

  .cardsInfor {
    list-style-type: none;
    text-align: start;
  }

  .cardsInfor li {
    font-size: 0.373333rem;
    line-height: 0.7rem;
  }

  .textOrange {
    color: #ff8309;
    font-size: 0.373333rem;
    line-height: 1rem;
  }

  .footer {
    width: 100%;
    height: 1.73333rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    bottom: 0;
    left: 0;
    border-top: 1px solid #f5f5f5;
  }

  .price {
    font-size: 0.373333rem;
    margin-left: 0.4rem;
    color: #ff8309;
  }

  .buyBtn {
    height: 100%;
    border: none;
    outline: none;
    padding: 0 0.4rem;
    color: #333333;
    background: #ffd200;
  }

  .mask {
    z-index: 9999;
    position: absolute;
    top: -10rem;
    width: 100%;
    height: 2000%;
    background: #ffffff;
    opacity: 0;
  }

  .moreMenu {
    position: absolute;
    list-style-type: none;
    background: #f5f5f5;
    top: 1.2rem;
    right: 0.5rem;
    border-radius: 0.2rem;
    z-index: 99;
    padding: 0 0.65rem;
    box-shadow: -1px 1px 3px #f5f5f5;
  }

  .moreMenu li {
    width: 1.466667rem;
    color: #ff8309;
    padding: 0.5rem 0;
    font-size: 0.4266667rem;
  }

  .moreMenu li:nth-child(2) {
    border-top: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
  }
</style>



